@import '../../../GlobalColors.scss';

@media (min-width: 2560px) {

}
@media (min-width: 1024px) and (max-width: 2559px) {
.footer {
    height: 315px;
    width: 100%;
    background-color: #283593;
    position: relative;
    margin: 0;
    .footer-container {
        margin-top: -10px;
        padding-left: 52px;
        padding-right: 52px;

    }
    hr {
        transform: translateY(85px)
    }
    a {
        font-size: 16px;
        color: white;
        font-weight: 400;
    }
    .fotter_navigation {
        height: 85px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .footer_navigation-logo {
            margin-left: -25px;
        }
        a {
            margin-left: 25px;
        }
        a:hover {
            color: $primary;
        }
    }
    .footer_info {
        margin-top: 30px;
        p {
            margin: 0;
        }
        .footer-contact {
            // padding-left: 10px;
            transform: translateX(10px);
            div {
                // height: 25px;
                margin-bottom: 18px;
                display: flex;
                align-items: center;
                p {
                    margin-left: 10px;
                    font-size: 20px;
                    transition: .2s;
                }     
            }
            div:hover {
                cursor: pointer;
                transition: .2s;
                p {
                    color: $primary;
                    transition: .2s;
                }
                svg {
                    filter: invert(27%) sepia(51%) saturate(3878%) hue-rotate(346deg) brightness(80%) contrast(97%);
                    transition: .2s;
                }
            }
        }
        .footer-category {
            display: flex;
            flex-direction: column;
            transform: translateX(-10px);
            a {
                text-align: right;
                margin-bottom: 10px;
            }
            a:hover {
                color: $primary
            }
        }
    }
    .footer_bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 40px;
        p {
            margin: 0;
            font-size: 11px;
        }
        .soc {
            display: flex;
            margin-top: -13px;
            margin-right: 1px;
            a{
                margin-left: 20px;
            }
            a:hover {
                filter: invert(27%) sepia(51%) saturate(3878%) hue-rotate(346deg) brightness(80%) contrast(97%)
            }
        }
    }
}
}
@media (min-width: 768px) and (max-width: 1023px) {
    footer {
        height: 315px;
        width: 100%;
        background-color: #283593;
        position: relative;
        margin: 0;
        .footer-container {
            margin-top: -10px;
            padding-left: 52px;
            padding-right: 52px;
    
        }
        hr {
            transform: translateY(85px)
        }
        a {
            font-size: 16px;
            color: white;
            font-weight: 400;
        }
        .fotter_navigation {
            height: 85px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transform: translateX(25px);

            .footer_navigation-logo {
                margin-left: -80px;
            }
            a {
                margin-left: 28px;
            }
            a:hover {
                color: $primary;
            }
        }
        .footer_info {
            margin-top: 30px;
            p {
                margin: 0;
            }
            .footer-contact {
                // padding-left: 10px;
                transform: translateX(-15px) translateY(-5px);
                div {
                    // height: 25px;
                    margin-bottom: 18px;
                    display: flex;
                    align-items: center;
                    p {
                        margin-left: 0px;
                        font-size: 20px;
                        transition: .2s;
                    }     
                    svg {
                        // filter: invert(27%) sepia(51%) saturate(3878%) hue-rotate(346deg) brightness(80%) contrast(97%);
