                }
                a:hover {
                    filter: invert(27%) sepia(51%) saturate(3878%) hue-rotate(346deg) brightness(80%) contrast(97%)
                }
            }
        }
    }
}

@media (max-width: 767px) {
    footer {
        height: 460px;
        width: 100%;
        background-color: #283593;
        position: relative;
        margin: 0;
        .footer-container {
            margin-top: -10px;
            padding-left: 52px;
            padding-right: 52px;
    
        }
        hr {
            transform: translateY(225px)
        }
        a {
            font-size: 16px;
            color: white;
            font-weight: 400;
        }
        .fotter_navigation {
            height: 225px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            .footer_navigation-logo {
                margin-left: 0px;
                transform: translateY(30px);

            }
            .fotter_navigation-list {
                display: flex;
                flex-direction: column;

                a {
                    transform: translateY(-29px);
                    margin-top: 6px;
                    text-align: center;
                }
            }
        }
        .footer_info {
            margin-top: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            p {
                margin: 0;
            }
            .footer-contact {
                transform: translateY(-7px);
                
                div {
                    margin: auto;
                    margin-bottom: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    p {
                        margin-left: 0px;
                        font-size: 18px;
                        transition: .2s;
                        text-align: center;
                    }     
                    svg {
                        display: none;
                    }
                }
            }
            .footer-category {
                display: none;
            }
        }
        .footer_bottom {
            display: flex;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
            margin-top: 25px;
            p {
                font-size: 11px;
                font-weight: 400;
                
                padding-top: 0px;
                
            }
            .soc {
                display: flex;
                margin-top: -5px;
                a{
                    margin: 3px 12px 3px 10px;
                }
            }
        }
    }
}