@media (min-width: 2560px) {
    .navigation {
        height: 65px;
        width: 100%;
        top: 0;
        position: fixed;
        background: white;
        z-index: 10;
    
        .navigation-container {
            padding-left: 16px;
            padding-right: 16px;
        }

        &-wrap {
            background: white;
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 100%;
            padding-left: 0px;
            padding-right: 0px;
    
            .navigation-logo {
                height: 24px;
            }
    
            .navigation-user {
                display: flex;
                align-items: center;
                .user-info {
                    width: 200px;
                    margin-right: 10px;
                    p {
                        color: black;
                        margin: 0;
                        text-align: right;
                    }
                    .user-name {
                        font-size: 14px;
                        font-weight: 600;
                        font-family: 'Source Sans Pro';
                    }
                    .user-email {
                        font-size: 12px;
                        font-weight: 400;
                        color: #8d8c8c;
                        font-family: 'Source Sans Pro';
                    }
                }
    
                .user-img {
                    height: 40px;
                    width: 40px;
                    background: grey;
                    border-radius: 50px;
                    margin-right: 13px;
                }
                .logout-button {
                    background-image: url(../../assets/img/icons/sign-out.svg);
                    width: 24px;
                    height: 20px;
                }
            }
    
            a {
                color: black;
                font-weight: 400;
                margin-right: 25px;
            }

            // line menu_button style
            button {
                display: none
            }
        }
    }    
}
