@media (min-width: 768px) and (max-width: 1023px) {
    .navigation {
        height: 65px;
        width: 100%;
        top: 0;
        position: fixed;
        background: white;
        z-index: 10;
    
        &-wrap {
            background: white;
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 100%;
            padding-left: 0px;
            padding-right: 0px;
    
            .navigation-logo {
                height: 24px;
            }
    
            .navigation-user {
                display: none;
            }

            a {
                display: none;
            }

            // line menu_button style
            button {
                transform: translateX(15px);
                box-shadow: none;
                background: none;
            }
            button:active {
                box-shadow: none;
                background: none;
            }
            button:hover {
                box-shadow: none;
                background: none;
            }
        }
    }    

    // line menu style
    .menu {
        width: 34vw;
        height: 100%;
        .menu-wrap {
            margin-top: 35px;
            margin-left: 25px;
            hr {
                margin-left: -25px; 
            }
            .user-img {
                height: 65px;
                width: 65px;
                border-radius: 50px;
                background: grey;
                margin-bottom: 15px;
            }
            .user-info {
                margin-bottom: 25px;
                width: 200px;
                p {
                    color: black;
                    margin: 0;

                    &.user-name {
                        font-size: 18px;
                        font-family: 'Source Sans Pro';
                    }
                    &.user-email {
                        margin-top: 5px;
                        font-size: 12px;
                        color: #8d8c8c;
                        font-family: 'Source Sans Pro';
                    }
                }
            }
            nav {
                margin-top: 30px;
                display: flex;
                flex-direction: column;
                a {
                    color: black;
                    font-weight: 400;
                    margin-bottom: 21px;
                }
            }
        } 
    }
}
